import {Component, ElementRef, ViewChild} from '@angular/core';
import {Content, IonicPage, NavController, NavParams} from 'ionic-angular';
import {Observable} from "rxjs-compat";
import {AngularFirestore, AngularFirestoreCollection} from "@angular/fire/firestore";
import * as moment from "moment";
import {AngularFireAuth} from "@angular/fire/auth";
import {MembersProf} from "../../../providers/memberProf/members-prof";
import {FcmProvider} from "../../../providers/fcm/fcm";
import {Api} from "../../../providers/api/api";
import {Storage} from "@ionic/storage";


interface Prayer {
  docid: string;
  title: string,
  message: string,
  time: number | string;
  messageId: string;
  userId: string;
  userName: string;
  userAvatar: string;
  toUserId: string;

  status: string;
}

@IonicPage()
@Component({
  selector: 'page-chat',
  templateUrl: 'chat.html',
})
export class Chat {

  @ViewChild(Content) content: Content;
  @ViewChild('chat_input') messageInput: ElementRef;


  id: string;
  name?: string;
  avatar?: string;
  messages: any;
  model: any = {};
  editorMsg = '';showEmojiPicker = false;
  private item: any;
  prayers: Observable<Prayer[]>;
  prayersCollectionRef: AngularFirestoreCollection<Prayer>;
  private dtt = moment().format('YYYY-MM-DD HH:m:s');
  pId: string;
  private pid = Date.now();
    private uid: string;
    private userinfo: any = {};
    private dp: any;

  constructor(navParams: NavParams,
              private navCtrl: NavController,
              public afAuth: AngularFireAuth,
              public afs: AngularFirestore,
              private user: MembersProf,
              private fcm: FcmProvider,
              private churchname: Api,
              private sqlstorage: Storage,


  ) {
    this.item = navParams.get('data') || "lil";
    this.pId = this.item.docid;
   // console.log("idddddd"+this.pId);

      this.checkAuth()
      this.loadData()
      this.sqlstorage.get('prof_pic').then((val) => {
          this.dp = val

      });
  }

    async checkAuth () {
        this.afAuth.authState.subscribe(res => {
            if (res && res.uid) {
                this.uid =  res.uid
                this.user.query(res.uid).then((res: any) => {
                    this.userinfo = JSON.parse(res.data)

                }).catch(er => console.log(er))
                ;
            } else {
                this.navCtrl.setRoot('login',{
                    refresh:"true",
                });
            }
        });
    }
  
  async loadData(){
      this.prayersCollectionRef = this.afs.collection(this.churchname.churchname).doc("cms").collection("prayers").doc(this.pId)
          .collection("replies" ,ref => ref.orderBy("id","asc"));
      this.prayers = this.prayersCollectionRef.snapshotChanges().map
      (changes => {
          return changes.map(
              a => {
                  const data = a.payload.doc.data() as Prayer;
                  data.docid = a.payload.doc.id;

                  return data
              }
          )
      });
      this.fcm.subscribeToTopic(this.pid).catch(err=>{

      });

  }
  addocument(model: any) {

    console.log(model);

    this.prayersCollectionRef.add(model)
      .then(()=>
      {

      }).catch((error: any) => {
      console.log(error);

    });

  }

  ionViewWillLeave() {
    // unsubscribe
  }

  ionViewDidEnter() {
    //get notice list




  }

  onFocus() {
   // this.showEmojiPicker = false;
    this.content.resize();
    this.scrollToBottom();
  }


  sendMsg() {
    if (!this.editorMsg.trim()) return;

    // Mock notice
    const id = Date.now().toString();

    this.editorMsg = '';



  }



  

  scrollToBottom() {
    setTimeout(() => {
      if (this.content.scrollToBottom) {
        this.content.scrollToBottom();
      }
    }, 400)
  }

  private focus() {
    if (this.messageInput && this.messageInput.nativeElement) {
      this.messageInput.nativeElement.focus();
    }
  }

  private setTextareaScroll() {
    const textarea =this.messageInput.nativeElement;
    textarea.scrollTop = textarea.scrollHeight;
  }
}



